import React, {useEffect, useContext, useState} from 'react'
import { createCommunication } from '../../../lib/server'
import { User, Employer } from '../../../model/model'
import {UserContext} from '../../../lib/UserProvider'
import {Container} from '../../General/Container'
import Loading from '../../General/Loading'
import {Table, Button} from 'antd'
import DesignerInfoModal from './DesignerInfoModal'


const {Column} = Table

interface FindDesignerListType {
    name: string,
    score: number,
    expiration: string,
    primary_skill: string,
    contact: User
}


interface DesignerListProps {
    designerList: FindDesignerListType[]
}

const columns = [
    {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
    },
    {
        title: 'Score',
        dataIndex: 'score',
        key: 'score',
    },
    {
        title: 'Expiration',
        dataIndex: 'expiration',
        key: 'expiration',
    },
    {
        title: 'Experience',
        dataIndex: 'experience',
        key: 'experience',
    },
    {
        title: 'Primary Skill',
        dataIndex: 'primary_skill',
        key: 'primary_skill',
    },
    {
        title: 'Contact',
        dataIndex: 'contact',
        key: 'contact',
    },
]

const DesignerList: React.FC<DesignerListProps> = (props) => {
    const {currentUser, changeUser}  = useContext(UserContext)
    const {designerList} = props
    const [showModal, setModalVisibility] = useState(false) 
    const [currentDesigner, setCurrentDesigner] = useState<User>(undefined) 

    const onMoreInfo = (user: User) => {
        setCurrentDesigner(user)
        setModalVisibility(true)
    }

    const onConnect = async (designerId: string) => {
        window.analytics.track((currentUser as Employer).companyName + ' connects to designer');
        const comm = await createCommunication(designerId, currentUser.id)
        setModalVisibility(false)
        const newUser = {...currentUser, communications: [...currentUser.communications, comm.id]}
        changeUser(newUser)
    }

    return (
        <>
            <DesignerInfoModal setInvisible={() => setModalVisibility(false)}
                                visible={showModal}
                               designer={currentDesigner}
                               onConnect={onConnect}/>
            <Table
                style={{width: '100%'}}
                size={"large"}
                dataSource={designerList}
                bordered={false}
                columns={columns}
            >
                <Column title="Name" dataIndex="name" key="name" />
                <Column title="Score" dataIndex="score" key="score" />
                <Column title="Expiration" dataIndex="expiration" key="expiration" />
                <Column title="Primary Skill" dataIndex="primary_skill" key="primary_skill" />
                <Column title="" dataIndex="contact" key="contact" render={(contact) => (
                    <Button onClick={() => onMoreInfo(contact)}>
                        More Info
                    </Button>
                )} />
            </Table>
        </>)
}

export default DesignerList